import { useState, useEffect } from 'react';
// External File
import API from '../api';

export default function Data()  {
  const [userActivity, setUserActivity] = useState([]);

  const fetchUserActivity = async (user) => {
    try {
      const response = await API.get(`${process.env.REACT_APP_API_URL}/data`, {
        withCredentials: true,
        params: {
          userId: user.uid,
        },
      });
      setUserActivity(response.data);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div>
      <div>
        {userActivity.length > 0 && userActivity[0] ? (
          <div>
            <h2>ID: {userActivity[0].userId}</h2>
            <h2>: {userActivity[0].loginCount}</h2>
            <h2>ToDo: {userActivity[0].addCount}</h2>
            <h2>ToDo: {userActivity[0].finishCount}</h2>
            <h2>: {userActivity[0].completePomodoros}</h2>
          </div>
        ) : (
          <p>...</p>
        )}
      </div>
    </div>
  );
};